<template>
<div style="display:inline-block">
  <mu-circular-progress :size="50" :strokeWidth="5" style="position: fixed;z-index: 999;left: 50%;margin-left: -25px;top:30%"  v-if="isloading"/>
  <mu-raised-button class="demo-raised-button" label="">
    <input type="file" class="file-button" accept="image/*" @change="uploadImage($event)">
  </mu-raised-button>
</div>
</template>
<script>
import lrz from 'lrz'
export default {
  props: {
    width: Number
  },
  data () {
    return {
      isloading: false,
      result: ''
    }
  },
  methods: {
    uploadImage (e) {
      let _this = this
      lrz(e.target.files[0], {width: _this.width || 800, fieldName: 'Filedata'})
        .then(function (rst) {
          // 
          /* ==================================================== */
          // ajax (_)
          // jQueryformDatagooglebaidu
          let xhr = new XMLHttpRequest()
          xhr.open('POST', '/rest/api/album/fileupload')
          xhr.onload = function () {
            _this.isloading = false
            if (xhr.status === 200) {
              // 
              let result = JSON.parse(xhr.response).attributes.data
              console.log(result)
              _this.result = result
              _this.$emit('result', result)
            } else {
              // 
              console.log(xhr.statusText)
            }
          }
          xhr.onerror = function () {
            // 
          }
          xhr.upload.onprogress = function (e) {
            // 
            _this.isloading = true
            // var percentComplete = ((e.loaded / e.total) || 0) * 100
          }
          // 
          rst.formData.append('fileLen', rst.fileLen)
          // 
          xhr.send(rst.formData)
          /* ==================================================== */
          return rst
        })
        .catch(function (err) {
          console.log(err)
          // 
        })
        .always(function () {
          _this.isloading = false
          // 
        })
    }
  }
}
</script>



