<template>
  <div>
    <mu-appbar title=''>
      <mu-icon-button icon='arrow_back' @click='back' slot='left'/>
    </mu-appbar>
    <div class='container p10'>
      <p>
        <div>
          
        </div>
        <mu-radio name='type' v-model='bind.type' v-for='item in typeList' :label='item.text' :nativeValue='item.value' class='demo-radio' labelClass='w-radio'/>
        <mu-text-field type='url' label='' hintText='http://' v-model='bind.address'/>
        <mu-text-field label='icp' hintText='' v-model='bind.icp'/>
      </p>
      <mu-raised-button label='' @click='submit' class='submit-raised-button' secondary fullWidth/>
    </div>

  </div>
</template>
<script>
import qs from 'qs'
export default {
  data () {
    return {
      isloading: true,
      typeList: [
        {text: '', value: 'D'},
        {text: '', value: 'A'},
        {text: '', value: 'I'},
        {text: '', value: 'M'}
      ],
      bind: {
        type: 'D',
        icp: this.$store.state.enterprise.icp || ''
      }
    }
  },
  methods: {
    back () {
      this.$router.back()
    },
    submit () {
      if (!this.bind.address) {
        window.alert('')
        return
      }
      if (!this.bind.icp) {
        window.alert('icp')
        return
      }
      if (!this.bind.icp) {
        window.alert('icp')
        return
      }
      const reg = new RegExp('[0-9a-zA-Z]+[0-9a-zA-Z\\.-]*\\.[a-zA-Z]{2,4}')
      if (!reg.test(this.bind.address)) {
        window.alert('')
        return
      }
      this.$http.post('/rest/api/bind/detail?' + qs.stringify(this.bind)).then((res) => {
        this.$router.push({
          path: '/bind',
          query: this.bind
        })
      })
    }
  }
}
</script>
<style lang='less'>
.demo-radio .mu-radio-icon {
    margin-right: 5px;
}
.demo-radio .w-radio{
  font-size: 14px;padding-right: 10px;
}
</style>



