<template>
  <div>
    <mu-appbar title=''>
      <mu-icon-button icon='arrow_back' @click='back'  slot='left'/>
    </mu-appbar>
    <mobile-tear-sheet>
      <mu-list>
        <mu-list-item title="" href="#/account">
          <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
        </mu-list-item>
        <mu-divider />
        <mu-list-item title="" href="#/service_feedback">
          <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
        </mu-list-item>
        <mu-divider />
        <mu-list-item title="" href="#/account">
          <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
        </mu-list-item>
        <mu-divider />
      </mu-list>

      <mu-list>
        <mu-list-item title="" href="http://m.jihui88.com/">
          <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
        </mu-list-item>
        <mu-divider />
        <mu-list-item title="" href="#/sale">
          <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
        </mu-list-item>
        <mu-divider />
        <mu-list-item title="" href="#/service">
          <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
        </mu-list-item>
        <mu-divider />
        <mu-list-item title="" href="#/about">
          <mu-icon value="navigate_next" :size="20" slot="right" color="#aaa"/>
        </mu-list-item>
        <mu-divider />
      </mu-list>
      <mu-raised-button label="" @click="signout" class="submit-raised-button" secondary fullWidth/>
    </mobile-tear-sheet>

  </div>
</template>
<script>
export default {
  methods: {
    back () {
      this.$router.back()
    },
    signout () {
      this.$http.get('/rest/api/user/logout').then((res) => {
        this.$store.state.user = null
        this.$router.push({path: '/login'})
      })
    }

  }
}
</script>



